import React from 'react';
import Loader from "react-loader-spinner";
import style from './Loader.module.scss';


const  LoaderSpinner = () => {
    return (
    <Loader
    className={style.LoaderSpinner}
        type="Puff"
        color="#00BFFF"
        height={100}
        width={100}
        timeout={2000} 
    />
    );
}
export default LoaderSpinner;